<template>
  <el-card class="coupon_wrapper">
    <el-button @click="addCoupon">{{ $t("btn.addCoupon") }}</el-button>
    <coupon-dialog
      :couponType="1"
      :visible.sync="couponDialogVisible"
      @coupons="addNewCoupon"
    ></coupon-dialog>
    <el-divider></el-divider>

    <el-pagination
      @current-change="currentChange"
      layout="total, prev, pager, next"
      :total="couponTotalNum"
      @prev-click="prevClick"
      @next-click="nextClick"
      background
      :pager-count="7"
    >
    </el-pagination>
    <el-table
      :data="couponList"
      border
      highlight-current-row
      style="width: 100%"
      rules="couponRules"
    >
      <el-table-column
        align="center"
        :label="$t('credit.tableName')"
        width="200"
        prop="coupon_money"
      >
        <template slot-scope="{ row }">
          <span>{{ row.coupon_money + $t("credit.coupon") }}</span>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="types"
        :label="$t('coupon.type')"
        width="80"
      >
        <template slot-scope="{ row }">
          <span>{{
            row.types === 0 ? $t("coupon.all") : $t("coupon.member")
          }}</span>
        </template>
      </el-table-column>

      <el-table-column
        align="center"
        :label="$t('credit.tableStartDate')"
        width="250"
        prop="startdate"
      >
        <template slot-scope="{ row }">
          <span>{{ $d(new Date(row.startdate), "long") }}</span>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        :label="$t('credit.tableEndDate')"
        width="250"
        prop="enddate"
      >
        <template slot-scope="{ row }">
          <span>{{ $d(new Date(row.enddate), "long") }}</span>
        </template>
      </el-table-column>
      <el-table-column :label="$t('btn.action')" width="200" align="center">
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="mini"
            style="width: 100px"
            :loading="scope.row.loading"
            @click="deleteCoupon(scope)"
            >{{ $t("btn.delete") }}</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </el-card>
</template>

<script>
import CouponDialog from "@/components/CouponDialog.vue";
import { operateCoupon } from "@/api/memberAPI.js";
import { mapState } from "vuex";
export default {
  async created() {
    let obj = {
      types: 0,
      operate_mode: "query",
      shop_id: this.shopId,
      pageNum: 10,
      page: 1,
    };
    let ret = await operateCoupon(obj);
    let newList = ret.data[1].map((item) => {
      item.loading = false;
      return item;
    });
    this.couponList = newList;
    this.couponTotalNum = ret.data[0];
  },
  data() {
    return {
      couponDialogVisible: false,
      couponList: [],
      couponTotalNum: 0,
    };
  },
  computed: {
    ...mapState({
      shopId: (state) => state.user.loginData.id,
    }),
  },
  methods: {
    async getOnePageCoupon(page) {
      let obj = {
        types: 0,
        operate_mode: "query",
        shop_id: this.shopId,
        pageNum: 10,
        page: page,
      };
      let ret = await operateCoupon(obj);
      let newList = ret.data[1].map((item) => {
        item.loading = false;
        return item;
      });
      this.couponList = newList;
      this.couponTotalNum = ret.data[0];
    },
    addCoupon() {
      this.couponDialogVisible = true;
    },
    currentChange(page) {
      this.getOnePageCoupon(page);
    },
    prevClick(page) {
      this.getOnePageCoupon(page);
    },
    nextClick(page) {
      this.getOnePageCoupon(page);
    },
    addNewCoupon(value) {
      let obj = {
        ...value,
        loading: false,
      };
      this.couponList.push(obj);
      this.couponTotalNum += 1;
    },
    deleteCoupon(scope) {
      scope.row.loading = true;
      let obj = { operate_mode: "delete", coupon_id: scope.row.id };
      operateCoupon(obj)
        .then(() => {
          scope.row.loading = false;
          this.couponList.splice(scope.$index, 1);
          this.couponTotalNum -= 1;
        })
        .catch((err) => {
          scope.row.loading = false;
          console.log(err);
        });
    },
  },
  components: {
    CouponDialog,
  },
};
</script>

<style lang="scss" scoped>
.coupon_wrapper {
  height: calc(100vh - 8em);
  overflow-y: auto;
}
</style>
