<template>
  <el-dialog :title="$t('credit.create')" :visible.sync="openCouponDialog">
    <el-form
      :model="couponForm"
      ref="couponForm"
      :rules="couponRules"
      label-width="80px"
      label-position="left"
      :inline="false"
      size="normal"
    >
      <el-form-item :label="$t('coupon.type')" size="normal" prop="types">
        <el-select v-model="couponForm.types" clearable filterable>
          <el-option
            v-for="item in couponTypes.types"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item :label="$t('credit.couponMoney')" prop="money">
        <el-input v-model="couponForm.money"></el-input>
      </el-form-item>

      <el-form-item :label="$t('credit.tableStartDate')" prop="start_date">
        <el-date-picker
          v-model="couponForm.start_date"
          type="datetime"
          size="normal"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item :label="$t('credit.tableEndDate')" prop="end_date">
        <el-date-picker
          v-model="couponForm.end_date"
          type="datetime"
          size="normal"
        >
        </el-date-picker>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button
        type="primary"
        size="default"
        @click="openCouponDialog = false"
        >{{ $t("btn.cancel") }}</el-button
      >
      <el-button
        type="primary"
        size="default"
        @click="createCoupon()"
        :loading="couponLoading"
        >{{ $t("btn.confirm") }}</el-button
      >
    </div>
  </el-dialog>
</template>

<script>
import { operateCoupon } from "@/api/memberAPI.js";
import { mapState } from "vuex";
export default {
  props: {
    couponType: {
      type: Number,
    },
    visible: {
      type: Boolean,
    },
  },

  data() {
    return {
      couponLoading: false,
      couponForm: {},
    };
  },

  methods: {
    createCoupon() {
      this.$refs.couponForm.validate((valid) => {
        if (valid) {
          this.couponLoading = true;

          this.openCouponDialog = true;
          let obj = {
            types: this.couponType,
            operate_mode: "insert",
            shop_id: this.shopId,
            ...this.couponForm,
            start_date: new Date(this.couponForm.start_date).getTime(),
            end_date: new Date(this.couponForm.end_date).getTime(),
          };
          operateCoupon(obj).then((ret) => {
            this.openCouponDialog = false;
            this.couponLoading = false;
            this.$message.success(this.$t("message.addSuc"));
            this.$refs.couponForm.resetFields();
            this.$emit("coupons", ret.data);
          });
        }
      });
    },
  },
  computed: {
    openCouponDialog: {
      get() {
        console.log(this.visible, "visible");
        return this.visible;
      },
      set(val) {
        console.log(val, "val");
        this.$emit("update:visible", val);
      },
    },
    ...mapState({
      shopId: (state) => state.user.loginData.id,
    }),
    couponRules() {
      return {
        types: [
          {
            required: true,
            message: this.$t("coupon.typesMsg"),
            trigger: "blur",
          },
        ],
        money: [
          {
            required: true,
            message: this.$t("credit.moneyMsg"),
            trigger: "blur",
          },
        ],
        start_date: [
          {
            required: true,
            message: this.$t("coupon.starDateMsg"),
            trigger: "blur",
          },
        ],
        end_date: [
          {
            required: true,
            message: this.$t("coupon.endDateMsg"),
            trigger: "blur",
          },
        ],
      };
    },
    couponTypes() {
      return {
        types: [
          {
            value: 0,
            label: this.$t("coupon.all"),
          },
          {
            value: 2,
            label: this.$t("coupon.member"),
          },
        ],
      };
    },
  },
};
</script>

<style lang="scss" scoped></style>
